import { Component, h, Element, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import type { ITooltipCell } from '../../../../types';

export interface CheckboxCellData {
    checked: boolean;
    disabled?: boolean;
}

export interface ICheckboxCellParams extends ICellRendererParams, ITooltipCell {
    checked?: (data: any) => boolean;
    setValue: (value: any) => void;
    getDisabled?: (data: any) => boolean;
}

/**
 * @category Table Cell
 * @description A checkbox cell component for table row selection and boolean data display. Provides accessible selection controls within data tables.
 */
@Component({
    tag: 'at-checkbox-cell',
    shadow: false,
})
export class AtCheckboxCellComponent implements ICellRendererComp {
    @Element() el: HTMLElement;

    private params!: ICheckboxCellParams;

    init(params: ICheckboxCellParams): void {
        this.params = params;
        this.params.api.refreshHeader();
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICheckboxCellParams): boolean {
        this.params = params;
        return true;
    }

    private setValue(checked: boolean): void {
        if (checked !== undefined && this.params?.setValue) {
            this.params.setValue(checked);
            this.params.api.refreshHeader();
        }
    }

    render() {
        const isChecked = this.params.checked
            ? this.params.checked(this.params.data)
            : false;
        const isDisabled = this.params.getDisabled
            ? this.params.getDisabled(this.params.data)
            : false;

        return (
            <Host class="flex h-full items-center">
                <at-checkbox
                    class="w-auto self-center"
                    disabled={isDisabled}
                    value={isChecked}
                    onAtuiChange={(event: CustomEvent) =>
                        this.setValue(event.detail)
                    }
                />
            </Host>
        );
    }
}
